import ListMap from '../util/listmap';
import mori from 'mori';

const handleMessageProxy = function(name, callback) {
    if (!this.messageHandlers) {
        this.messageHandlers = mori ? mori.hashMap() : {};
    }
    
    if (mori && mori.hashMap.isPrototypeOf(this.messageHandlers)) {
        this.messageHandlers = mori.assoc(this.messageHandlers, name, callback.bind(this));
    } else {
        this.messageHandlers[name] = callback.bind(this);
    }
}

function createComponent(name, options) {
    const component = {
        name: name,
        requiredComponents: options.requiredComponents || [],
        defaultData: (options.defaultData || options._) || {},
        entities: new ListMap(),
        messageHandlers: mori ? mori.hashMap() : {},
        _onAdd: options.onAdd || null,
        _onRemove: options.onRemove || null,
        _update: options.update || null,
        _aggregateUpdate: options.aggregateUpdate || null,
        _render: options.render || null,
        tags: options.tags || null,
        entityData: new ListMap(),
        version: options.version || '1.0.0',
        minVersion: options.minVersion || '1.0.0',
        maxVersion: options.maxVersion || null,
        syncState: options.syncState || false,
        syncProperties: options.syncProperties || []
    };

    // Bind methods to component instance
    component.handleMessage = handleMessageProxy.bind(component);
    component.update = function(callback) {
        this._update = callback;
    }.bind(component);
    component.onAdd = function(callback) {
        this._onAdd = callback;
    }.bind(component);
    component.onRemove = function(callback) {
        this._onRemove = callback;
    }.bind(component);
    component.render = function(callback) {
        this._render = callback;
    }.bind(component);
    component.aggregateUpdate = function(callback) {
        this._aggregateUpdate = callback;
    }.bind(component);

    // Add state synchronization methods
    component.getState = function(entity) {
        if (!this.syncState) return {};
        
        const state = {};
        this.syncProperties.forEach(prop => {
            if (prop in entity.data) {
                state[prop] = entity.data[prop];
            }
        });
        
        return state;
    }.bind(component);

    component.applyState = function(entity, state) {
        if (!this.syncState) return;
        
        Object.entries(state).forEach(([key, value]) => {
            if (this.syncProperties.includes(key)) {
                entity.setData(key, value);
            }
        });
    }.bind(component);

    component.handleStateUpdate = function(entity, data) {
        if (!this.syncState) return;
        
        if (data.delta) {
            this.applyState(entity, data.delta);
        }
    }.bind(component);

    // Register 'state-update' handler
    component.handleMessage('state-update', function(entity, data) {
        this.handleStateUpdate(entity, data);
    });

    // Add version comparison helper
    component.isVersionCompatible = function(version) {
        if (!version) return true;
        
        const v1 = this.version.split('.').map(Number);
        const v2 = version.split('.').map(Number);
        
        // Check min version
        if (this.minVersion) {
            const minV = this.minVersion.split('.').map(Number);
            if (v2[0] < minV[0] || (v2[0] === minV[0] && v2[1] < minV[1]) || 
                (v2[0] === minV[0] && v2[1] === minV[1] && v2[2] < minV[2])) {
                return false;
            }
        }
        
        // Check max version
        if (this.maxVersion) {
            const maxV = this.maxVersion.split('.').map(Number);
            if (v2[0] > maxV[0] || (v2[0] === maxV[0] && v2[1] > maxV[1]) || 
                (v2[0] === maxV[0] && v2[1] === maxV[1] && v2[2] > maxV[2])) {
                return false;
            }
        }
        
        return true;
    }.bind(component);

    return component;
}
class Component {
    constructor() {
        this.data = {};
        this.entity = null;
        this.messages = {};
    }

    onAdd() {
        // Override in subclasses
    }

    onRemove() {
        // Override in subclasses
    }

    sendMessage(message, data) {
        if (this.entity) {
            this.entity.sendMessage(message, data);
        }
    }
}

export default Component;
